@import url("https://fonts.googleapis.com/css?family=Montserrat:400,400i,700");

* {
	margin: 0;
	padding: 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-size: 62.5%;
}

@media (max-width: 75em) {
	html {
		font-size: 56.25%;
	}
}

@media (max-width: 56.25em) {
	html {
		font-size: 50%;
	}
}

@media (max-width: 37.5em) {
	html {
		font-size: 43.75%;
	}
}

@media (min-width: 112.5em) {
	html {
		font-size: 75%;
	}
}

body {
	font-family: "Montserrat", sans-serif;
	font-size: 1.6rem;
	color: #7678fb;
	font-weight: 400;
	line-height: 1.4;
	background-image: linear-gradient(to right bottom, #7775a5, #3c3b66);
	background-size: cover;
	background-repeat: no-repeat;
	min-height: 100vh;
}