.dropdown {
	position: relative;
}

.dropdown__input {
	width: 12rem;
	font-size: 1.4rem;
	text-align: left;
	padding: 0.7rem 1rem;
	color: #7678fb;
	background-color: #1c223a;
	border-radius: 3px;
	border: none;
	outline: none;
	cursor: pointer;
}

@media (max-width: 37.5em) {
	.dropdown__input {
		width: 8rem;
	}
}

.dropdown__content {
	position: absolute;
	width: 100%;
	height: 0;
	margin-top: 0.5rem;
	background-color: #1c223a;
	border-radius: 3px;
	z-index: 5;
	opacity: 0;
	visibility: hidden;
	transition: all 250ms ease-in;
}

.dropdown__item {
	text-decoration: none;
	display: block;
	padding: 0.5rem 1.5rem;
	cursor: pointer;
}

.dropdown__item:hover {
	background-color: #0c0f26;
}

.dropdown:hover .dropdown__content {
	height: 10rem;
	opacity: 1;
	visibility: visible;
}